<template>
  <div class="created_case_success">
    <listSearch 
      v-model="searchFormData"
      @searchConfirm="goSearch"
      :searchArr="searchArr">
    </listSearch>
    <div class="content">
      <div class="buttons">
        <Button type="primary" @click="exportList">列表导出</Button>
      </div>
      <div class="table_box mgt20">
        <Table ref="selectionTable" 
          :columns="columns"
          :data="tableData"
          show-sizer
          show-elevator
          show-total>
           <template slot-scope="{ row }" slot="proxyName">
            <span>{{ row.proxyName }}</span>
          </template>
          <template slot-scope="{ row }" slot="action">
            <div>
              <a href="javascript:;" style="margin-right:20px" @click="btnOperate(1,row)" title="查看文件">
               <img width="26" height="26" src="../../../assets/images/zhuting2.png" alt />
              </a>
              <a href="javascript:;" @click="btnOperate(2,row)" title="送达时间">
                <img width="26" height="26" src="../../../assets/images/time.png" alt />
              </a>
            </div>
          </template>
        </Table>
        <!-- 分页 -->
        <Page class="mgt20"
          :current="currentPage"
          :total="total"
          :page-size="pageSize"
          :page-size-opts="pageOpts"
          @on-change="changeCurrentPage"
          @on-page-size-change="changeSize"
          show-sizer
          show-elevator
          show-total/>
      </div>
    </div>
    <!-- 弹窗部分 -->
    <popupBox :show="popupShow" 
      :titleText="popupTypeObj[popupType]['title']"
      :width="'740'"
      @popupConfirm="popupConfirm" 
      @popupCancel="popupCancel"
      >
      <!-- 查看 -->
      <div class="file_dialog" v-if="popupType == 1">
        <Table  class="file_table" :columns="fileColumns" :data="fileTable">
            <template slot-scope="{ row }" slot="action">
              <a href="javascript:;"  @click="look(row)">
                <img width="22" height="22" src="../../../assets/images/zhuting2.png" alt="" title="预览">
              </a>
                <a href="javascript:;" style="margin-left:10px" @click="donloadFile">
                  <img width="22" height="22" src="../../../assets/images/dowload.png" alt="" title="下载"></a>
                
            </template>
        </Table>
        <div class="file_footer">
          <Page :current="fileCurrent" :total="fileTotal" :page-size="filePagesize" />
          <div class="close">
            <a href="javascript:;" @click="popupCancel">关闭</a>
            <a href="javascript:;">一键下载</a>
          </div>
        </div>
      </div>
      <!-- 送达时间 -->
      <div class="send_content" v-if="popupType == 2">
        <div class="item">
          <div class="item_content">
            <div class="label">请输入送达回证时间：</div>
            <div class="val">
              <DatePicker v-model="deliveryDate" 
              type="date" placeholder="选择时间"
              style="width: 200px"></DatePicker>
            </div>
          </div>
        </div>
      </div>
    </popupBox>
  </div>
</template>

<script>
import listSearch from '@/components/list-search/search.vue'
import {listMixin} from '../listMixin'
import {mapActions} from 'vuex'

const columns =  [
        {
          title: '案号',
          key: 'code',
          align: "center",
          width:200,
        },
        {
          title: '申请人',
          key: 'applicantName',
          align: "center"
        },
        {
          title: '代理人',
          key: 'proxyName',
          slot:"proxyName",
          align: "center"
        },
        {
          title: '被申请人',
          key: "respondentName",
          align: "center"
        },
        {
          title: '提交时间',
           key: "createTime",
          align: "center",
          width: 200
        },
        {
          title: '受理时间',
          key: 'acceptTime',
          align: "center"
        },
        {
          title: '案由',
          key: ''
        },
        {
          title: '操作',
          slot: 'action'
        }
      ]
export default {
  name: 'created_case_success',
  components: { listSearch },
  mixins: [listMixin],
  data () {
    return {
      popupShow: false,
      popupType: 2,
      popupTypeObj: {
        1: {type:1, title: '通过'},
        2: {type:2, title: '送达时间'},
        3: {type:3, title: '导出'}
      },
      deliveryDate: null, // 送达时间
      searchFormData: {
        personalName: null,
        companyName: null,
        checkbox: [],
        radio: null,
        status: null
      },
      searchArr: [
        {type: 'input', value: 'companyName', label: '案号', placeholder: '请输入企业名称'},
        {type: 'input', value: 'companyName', label: '申请人', placeholder: '请输入企业名称'},
        {type: 'input', value: 'companyName', label: '被申请人', placeholder: '请输入企业名称'},
        {type: 'select', value: 'status', label: '注册名称',
         arr: [
          {label: '通过', value: 1},
          {label: '未通过', value: 2}
         ], 
          placeholder: '请选择'
        },
        {type: 'select', value: 'status', label: '案件类型',
         arr: [
          {label: '通过', value: 1},
          {label: '未通过', value: 2}
         ], 
          placeholder: '请选择'
        },
        {type: 'select', value: 'status', label: '案由',
         arr: [
          {label: '通过', value: 1},
          {label: '未通过', value: 2}
         ], 
          placeholder: '请选择'
        }
      ],
      columns:columns,
      fileColumns:[
        // {
        //   title:'角色',
        //   key:"type"
        // },
        {
          title:"文件名",
          key:"fileName"
        },
        {
          title:"操作",
          slot: 'action',
          width: 150,
          align: 'center'
        }
      ],
      fileTable:[],
      fileCurrent:1,
      fileTotal:0,
      filePagesize:5
    }
  },
  mounted () {},
  methods: {
    ...mapActions({
      getRequsetList:"createCase/getRequsetList",
      filingCaseSuccessList:"createCase/filingCaseSuccessList",
      filingCaseSend:"createCase/filingCaseSend"
    }),
    // 搜索
    goSearch () {
      console.log(this.searchFormData)
    },
    exportList () {
      console.log('导出')
    },
    // 按钮此作
   async btnOperate (type,row) {
        if(type==1){
          // 查看文件
          const result =await this.filingCaseSuccessList({actionIdentifer:"UPCASE_DOCUMENT_LIST",caseId:row.id})
          if(result.data.flag){
            this.fileTable= result.data.data
            this.popupType = type
            this.popupShow = true
          } else {
           this.$Message.error(result.data.message);
          }
        } else {
          // 送达时间
          this.$Modal.confirm({
          title: "操作",
          content:
            `<div style="color:#3B4453;text-align:center;font-size:18px">您确定要送达吗?</div>`,
          onOk: async() => {
          
          const send = await  this.filingCaseSend({actionIdentifer:"UPCASE_SEND",caseId:JSON.stringify(new Array(row.id))});
             console.log(send);
            if (send.data.flag) {
                 this.$Message.success("操作成功");
                  this.getList()
                } else {
                  this.$Message.error(send.data.message);
                }
            
          },
          onCancel: () => {
          }
        });
        }
      
    },
    // 弹窗确认
    popupConfirm () {
      console.log(this.popupType, this.deliveryDate)
    },
    // 弹窗取消
    popupCancel () {
      console.log(1)
      this.reqestId='' // 重置
      this.popupShow = false
    },
    // 文件查看
    look (row) {
      var a = document.createElement('a');
        a.setAttribute('href', row.filePath);
        a.setAttribute('target', '_blank');
        document.body.appendChild(a);
        a.click();
    },
    // 下载
    donloadFile () {

    },
    async getList () {
       let params = {
        menuIdentifer: "UPCASE_SUCCESS_LIST", //菜单标识
        pageIndex: this.currentPage - 1,
        pageSize: this.pageSize
      };
      let filter ={//筛选条件
        applicantName:"",//申请人关键字
        respondentName:"",//被申请人关键字
        userId:'' ,//注册名称的用户id
        caseTypeId: '',//案件类型id
    	  createTimeBegin:"",//提交的开始时间
        createTimeEnd:"" //提交的结束时间
    }

      params = { ...params,filter:JSON.stringify(filter) };
      // console.log("参数", params);
      const res= await this.getRequsetList(params)
      if (res.data.flag) {
        console.log('待立案list',res.data.data);
        this.tableData = res.data.data.dataList;
        this.total = res.data.data.totalCount;
      }
    }
  }
}
</script>
<style lang="less">
.created_case_success {
  .content {
    margin-top: 30px;
    padding: 20px 10px;
    background: #fff;
    border-radius:4px;
    .buttons {
      padding-right: 20px;
      text-align: right;
    }
    .table_box {
      // padding: 0 2px;
    }
  }
  .mgt20 {
    margin-top: 20px;
  }
  .mgl10 {
    margin-left: 10px;
  }
}
.send_content {
  .item {
    .item_content {
      display: flex;
      margin-bottom: 6px;
      .label {
        width: 50%;
        flex: 1;
        text-align: right;
        font-size: 16px;
        font-family:PingFangSC-Regular,PingFang SC;
        font-weight:400;
        color: #708094;
      }
      .val {
        width: 50%;
        input {
          padding-left: 8px;
          border-radius:6px;
          border:1px solid rgba(156,178,205,1);
          font-size: 16px;
          font-family:PingFangSC-Regular,PingFang SC;
          font-weight:400;
          color: #708094;
        }
        span {
          font-size: 16px;
          font-family:PingFangSC-Regular,PingFang SC;
          font-weight:400;
          color: #708094;
        }
      }
    }
  }
}
.file_dialog{
  .file_footer {
    position: relative;
    margin-top: 10px;
  }
  .close{
    position: absolute;
    top: 10px;
    right: 0;
    a:first-child{
      margin-right: 20px;
      color: #000;
    }
  }
} 
</style>
<style lang="less">
@import url('../../../style/iview_table.less');
</style>